const demo = [
	{
		avatar: '/static/demo/demo66.jpg',
		username: '曲调',
		upaate_time: 1571187167,
		data: '内容内容内容内容内容内容内容内容内容内容内容内容内容内容内容',
		noread: 20
	},
	{
		avatar: '/static/demo/demo5.jpg',
		username: '阿拉善',
		upaate_time: 1571187190,
		data: '内容内容内容内容内容内容内容内容内容内容内容内容内容内容内容',
		noread: 18
	},
	{
		avatar: '/static/demo/demo5.jpg',
		username: '阿拉善',
		upaate_time: 1571187212,
		data: '内容内容内容内容内容内容内容内容内容内容内容内容内容内容内容',
		noread: 18
	},
	{
		avatar: '/static/demo/demo5.jpg',
		username: '阿拉善',
		upaate_time: 1571187264,
		data: '内容内容内容内容内容内容内容内容内容内容内容内容内容内容内容',
		noread: 18
	}
];
import msgList from '@/components/msg/msgList.vue';
import noThing from '@/components/common/noThing.vue';
import uniPopup from '@/components/uni-popup/uni-popup.vue';
export default {
	components: {
		msgList,
		noThing,
		uniPopup
	},
	data() {
		return {
			list: [
				{
					avatar: '/static/demo/demo5.jpg',
					username: '阿拉善',
					upaate_time: 1571187264,
					data: '内容内容内容内容内容内容内容内容内容内容内容内容内容内容内容',
					noread: 18
				}
			],
			type: 'top'
		};
	},
	//监听下拉刷新
	onPullDownRefresh() {
		this.refresh();
	},
	//监听原生导航栏按钮事件
	onNavigationBarButtonTap(e) {
		console.log(e);
		switch (e.index) {
			case 0: //左边
				uni.navigateTo({
					url:"../userList/userList"
				})
				this.$refs.popup.close();
				break;
			case 1: //右边
				this.$refs.popup.open();
				break;
		}
	},
	onLoad() {
		// console.log($T)
	},
	methods: {
		//下拉刷新
		refresh() {
			setTimeout(() => {
				this.list = demo;
				//停止下拉刷新
				uni.stopPullDownRefresh();
			}, 2000);
		},
		popupEvent(e) {
			switch (e) {
				case 'friend':
					console.log('添加好友');
					break;
				case 'clear':
					console.log('清除列表');
					break;
			}
			//关闭弹出层
			this.$refs.popup.close();
		}
	}
};
